<template>
  <LayoutPage class="show-viewport">
    <div class="viewport">
      <div class="viewport__main">
        <div class="title">
          <div class="hint">
            <div class="hint__icon" :class="device"></div>
          </div>
          your viewport size is :
        </div>
        <div class="document-size">
          {{ viewport.width }}<span> × </span> {{ viewport.height }}<span> px</span>
        </div>
      </div>
      <div class="viewport__other">
        <div class="browser">
          <div class="browser__title">Browser : {{ browserInfo.platform }}</div>
          <div class="browser__ctr">{{ browserInfo.name }} ( {{ browserInfo.version }} ) </div>
        </div>

        <div class="dpr">
          <div class="dpr__title">DPR : </div>
          <div class="dpr__number">{{ viewport.dpr }}</div>
        </div>

        <div class="screen">
          <div class="screen__title">Screen Size : </div>
          <div class="screen__size">{{ viewport.screenWidth }}<span> × </span> {{ viewport.screenHeight }}<span> px</span></div>
        </div>

      </div>
    </div>
  </LayoutPage>
</template>

<script setup>
// import Markdown from 'vue3-markdown-it';
import { reactive, defineOptions, onMounted, onBeforeUnmount, computed } from 'vue'
import LayoutPage from '@/components/layout/page'

defineOptions({ name: 'WhatIsMyViewport' })

// 瀏覽器資訊
const browserInfo = reactive({
  name: '',
  version: '',
  platform: '',
  desktop: null,
  mobile: null
})

const device = computed(() => browserInfo?.desktop ? 'hint__icon--desktop' : 'hint__icon--mobile')

// 視窗與螢幕資訊
const viewport = reactive({
  width: 0,
  height: 0,
  screenWidth: 0,
  screenHeight: 0,
  dpr: 0
})

// UA 偵測
const uaMatch = (ua) => {
  if (ua === undefined) {
    ua = window.navigator.userAgent;
  }
  ua = ua.toLowerCase();
  var match = /(edge)\/([\w.]+)/.exec(ua) ||
    /(opr)[\\/]([\w.]+)/.exec(ua) ||
    /(chrome)[ \\/]([\w.]+)/.exec(ua) ||
    /(iemobile)[\\/]([\w.]+)/.exec(ua) ||
    /(version)(applewebkit)[ \\/]([\w.]+).*(safari)[ \\/]([\w.]+)/.exec(ua) ||
    /(webkit)[ \\/]([\w.]+).*(version)[ \\/]([\w.]+).*(safari)[ \\/]([\w.]+)/.exec(ua) ||
    /(webkit)[ \\/]([\w.]+)/.exec(ua) ||
    /(opera)(?:.*version|)[ \\/]([\w.]+)/.exec(ua) ||
    /(msie) ([\w.]+)/.exec(ua) ||
    ua.indexOf("trident") >= 0 && /(rv)(?::| )([\w.]+)/.exec(ua) ||
    ua.indexOf("compatible") < 0 && /(mozilla)(?:.*? rv:([\w.]+)|)/.exec(ua) ||
    [];
  var platform_match = /(ipad)/.exec(ua) ||
    /(ipod)/.exec(ua) ||
    /(windows phone)/.exec(ua) ||
    /(iphone)/.exec(ua) ||
    /(kindle)/.exec(ua) ||
    /(silk)/.exec(ua) |
    /(android)/.exec(ua) ||
    /(win)/.exec(ua) ||
    /(mac)/.exec(ua) ||
    /(linux)/.exec(ua) ||
    /(cros)/.exec(ua) ||
    /(playbook)/.exec(ua) ||
    /(bb)/.exec(ua) ||
    /(blackberry)/.exec(ua) ||
    [];
  var browser = {},
    matched = {
      browser: match[5] || match[3] || match[1] || "",
      version: match[2] || match[4] || "0",
      versionNumber: match[4] || match[2] || "0",
      platform: platform_match[0] || ""
    };
  var ie = null;
  if (matched.browser) {
    browser[matched.browser] = true;
    browser.version = matched.version;
    browser.versionNumber = parseInt(matched.versionNumber, 10);
  }
  if (matched.platform) {
    browser[matched.platform] = true;
  }
  if (browser.android || browser.bb || browser.blackberry || browser.ipad || browser.iphone ||
    browser.ipod || browser.kindle || browser.playbook || browser.silk || browser["windows phone"]) {
    browser.mobile = true;
  }
  if (browser.cros || browser.mac || browser.linux || browser.win) {
    browser.desktop = true;
  }
  if (browser.chrome || browser.opr || browser.safari) {
    browser.webkit = true;
  }
  if (browser.chrome) {
    var chrome = "Chrome"
    matched.browser = chrome
    browser[chrome] = true;
  }
  if (browser.msie) {
    ie = "IE";
    matched.browser = ie;
    browser[ie] = true;
  }
  if (browser.rv || browser.iemobile) {
    ie = "IE";
    matched.browser = ie;
    browser[ie] = true;
  }
  if (browser.edge) {
    delete browser.edge;
    var msedge = "Edge";
    matched.browser = msedge;
    browser[msedge] = true;
  }
  if (browser.safari && browser.blackberry) {
    var blackberry = "blackberry";
    matched.browser = blackberry;
    browser[blackberry] = true;
  }
  if (browser.safari && browser.playbook) {
    var playbook = "playbook";
    matched.browser = playbook;
    browser[playbook] = true;
  }
  if (browser.bb) {
    var bb = "blackberry";
    matched.browser = bb;
    browser[bb] = true;
  }
  if (browser.opr) {
    var opera = "Opera";
    matched.browser = opera;
    browser[opera] = true;
  }
  if (browser.safari && browser.android) {
    var android = "Android";
    matched.browser = android;
    browser[android] = true;
  }
  if (browser.safari && browser.kindle) {
    var kindle = "kindle";
    matched.browser = kindle;
    browser[kindle] = true;
  }
  if (browser.safari && browser.silk) {
    var silk = "silk";
    matched.browser = silk;
    browser[silk] = true;
  }
  browser.name = matched.browser;
  browser.platform = matched.platform;
  return browser;
}

// 更新視窗資訊
const updateSizes = () => {
  const result = uaMatch(window.navigator.userAgent)
  browserInfo.name = result.name
  browserInfo.version = result.version
  browserInfo.platform = result.platform
  browserInfo.desktop = result.desktop ? true : null
  browserInfo.mobile = result.mobile ? true : null

  viewport.width = document.documentElement.clientWidth
  viewport.height = document.documentElement.clientHeight
  viewport.screenWidth = window.screen.width
  viewport.screenHeight = window.screen.height
  viewport.dpr = window.devicePixelRatio.toFixed(2)
}

onMounted(() => {
  updateSizes()
  window.addEventListener('resize', updateSizes)
})

onBeforeUnmount(() => {
  window.removeEventListener('resize', updateSizes)
})
</script>

<style lang="sass" scoped>
@import @sass/init
@import @sass/helpers

@import ./style
</style>
